import { Component, OnInit,ViewChild ,ElementRef} from '@angular/core';
import { CommonService } from './services/common.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent implements OnInit {
  title = 'app';
  products;
	x1="categories"
  y1=""

  x2="brands"
  y2=""

  x3="tags"
  y3=""

  x4="sizes"
	y4=""

	x5="colors"
	y5=""
	

	@ViewChild("f1")f1:ElementRef;
  @ViewChild("f2")f2:ElementRef;
  @ViewChild("f3")f3:ElementRef;
	@ViewChild("f4")f4:ElementRef;
	@ViewChild("f5")f5:ElementRef;

 
  constructor(private cs:CommonService){}

  ngOnInit() {
    this.cs.getProducts().subscribe(d=>this.products=d);    
  }
	search()
	{

		let f1=this.f1.nativeElement.value.trim()
  	let f2=this.f2.nativeElement.value.trim()
  	let f3=this.f3.nativeElement.value.trim()
		let f4=this.f4.nativeElement.value.trim()
		let f5=this.f5.nativeElement.value.trim()
  	this.y1=f1
  	this.y2=f2
  	this.y3=f3
		this.y4=f4
		this.y5=f5
	}	
  
}
